#container {
  display: grid;
  grid: repeat(3, 100px) / auto-flow 100px;
}

#container > div {
  background-color: #8ca0ff;
  width: 100px;
  height: 50px;
}
.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.wrappe > div {
  border: solid red;
  /* width: 50px; */
  background-color: darkblue;
}
.one {
  border: solid red;
  /* width: 50px; */
  background-color: darkblue;
  grid-column: 1 / 3;
  grid-row: 1;
}
.two {
  grid-column: 2 / 4;
  grid-row: 1 / 3;
}
.three {
  grid-column: 1;
  grid-row: 2 / 5;
}
.four {
  grid-column: 3;
  grid-row: 3;
}
.five {
  grid-column: 2;
  grid-row: 4;
}
.six {
  grid-column: 3;
  grid-row: 4;
}

