/* footer section  */
#footer-section {
  background-color: rgb(95, 95, 95);
  color: #ffffff;
  width: 100%;
}
ul {
  list-style: none;
}
#footer-section img {
  width: 150px;
  margin: 20px;
}
#footer-section .footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  /* background-color: aquamarine; */
  /* margin: auto; */
}
#footer-section .footer-container .item {
  width: 50%;
  margin: 10px 20px;
  max-width: 250px;
}
#footer-section .footer-container .item .about-text {
  padding-left: 10px;
}
#footer-section .footer-container .item .icon {
  width: 20px;
  margin: 5px;
  padding: 0;
  filter: invert();
}
#footer-section .footer-container .item span {
  display: block;
  font-size: large;
  /* font-weight: 20px; */
  font-weight: bold;
  border-bottom: solid 5px #00b0ff;
  /* width: 200px; */
  /* float: right; */
}
#footer-section .footer-container .item .social-icons ul {
  /* float: right; */
  /* border: solid; */
  margin: 0;
  padding: 0;
  list-style: none;
}
#footer-section .footer-container .item .social-icons ul li {
  float: left;
  margin: 0;
  padding: 0;
}
#footer-section .footer-container .item .social-icons ul li img {
  width: 20px;
  margin: 10px;
  padding: 0;
  filter: invert();
}
