@media (min-width: 0px) and (max-width: 425px) {
  #footer-section .footer-container .item {
    width: 100%;
    margin: 10px 20px;
    max-width: 250px;
  }
  #footer-section .footer-container .item .social-icons ul {
    /* border: #ffffff solid; */
    margin: 0;
    float: none;
  }
}
